import React, { useState, useRef } from "react";
import Webcam from "react-webcam";
import Tesseract from "tesseract.js";
import "./App.css";

const App = () => {
  const [image, setImage] = useState(null); // Store the captured image
  const [ocrText, setOcrText] = useState(""); // Store the OCR result
  const [processedText, setProcessedText] = useState(""); // Store the API response
  const [isProcessing, setIsProcessing] = useState(false); // Track OCR processing state
  const webcamRef = useRef(null); // Webcam reference

  // Capture image from webcam
  const captureImage = () => {
    const imageSrc = webcamRef.current.getScreenshot(); // Capture image
    setImage(imageSrc); // Save image to state
    setOcrText(""); // Clear previous OCR text
    setProcessedText(""); // Clear previous API response
  };

  // Reset camera to allow new capture
  const resetImage = () => {
    setImage(null); // Clear image
    setOcrText(""); // Clear OCR text
    setProcessedText(""); // Clear API response
  };

  // Perform OCR on the captured image
  const performOcr = () => {
    if (!image) return;
    setIsProcessing(true); // Show processing state
    Tesseract.recognize(
      image, // Image to process
      "eng", // Language code (English)
      {
        logger: (info) => console.log(info), // Log OCR progress (optional)
      }
    )
      .then(({ data: { text } }) => {
        setOcrText(text); // Save extracted text
        sendOcrToApi(text); // Send text to backend API
        setIsProcessing(false); // Stop processing state
      })
      .catch((error) => {
        console.error("OCR Error:", error);
        setIsProcessing(false); // Stop processing state
      });
  };

  // Send OCR result to backend API
  const sendOcrToApi = async (text) => {
    try {
      const response = await fetch("http://127.0.0.1:8000/api/process-text/", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ ocr_text: text }),
      });

      if (response.ok) {
        const data = await response.json();
        setProcessedText(data.processed_text); // Save the response from the API
      } else {
        console.error("API Error: ", response.statusText);
      }
    } catch (error) {
      console.error("Error sending data to API:", error);
    }
  };

  return (
    <div className="app">
      {/* Header */}
      <header className="header">
        <h1>React OCR Camera App</h1>
      </header>

      {/* Main Content */}
      <main className="main-content">
        {processedText ? (
          // Display Processed Text from API
          <div className="ocr-result">
            <h2>Processed Text</h2>
            <p>{processedText}</p>
          </div>
        ) : ocrText ? (
          // Display OCR Result
          <div className="ocr-result">
            <h2>Scanned Text</h2>
            <p>{ocrText}</p>
          </div>
        ) : image ? (
          // Display Captured Image with Buttons
          <div className="image-container">
            <img src={image} alt="Captured" className="captured-image" />
            <div className="button-group">
              <button className="capture-btn" onClick={resetImage}>
                Reclick
              </button>
              <button
                className="capture-btn scan-btn"
                onClick={performOcr}
                disabled={isProcessing}
              >
                {isProcessing ? "Scanning..." : "Scan"}
              </button>
            </div>
          </div>
        ) : (
          // Display Camera Feed
          <Webcam
            ref={webcamRef}
            audio={false}
            screenshotFormat="image/jpeg"
            className="webcam"
          />
        )}

        {!image && !ocrText && !processedText && (
          <button className="capture-btn" onClick={captureImage}>
            Scan
          </button>
        )}
      </main>

      {/* Footer */}
      <footer className="footer">
        <p>&copy; 2024 React OCR Camera App</p>
      </footer>
    </div>
  );
};

export default App;
